.header {
    max-width: 1430px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    &-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.logo {
    width: 209px;
    height: 94px;
    background-color: $black;
    background-image: url(../../../img/svg/logo.svg);
    background-position: center;
    background-repeat: no-repeat;
    margin-right: 30px;
}
.menu {
    width: 40px;
    height: 19px;
    background-image: url(../../../img/svg/menu.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 66px -13px 4px;
    padding: 0;
    cursor: pointer;
    &:hover {
        background-image: url(../../../img/svg/menu-black.svg);
    }
}
.nav {
    display: flex;
    list-style-type: none;
    &-item {
        margin-right: 17px;
    }
    &-link {
        text-decoration: none;
        color: $white;
        transition: 0.3s ease;
        &:hover {
            color: $violet;
        }
    }
}
.header-right {
    display: flex;
    color: $white;
    list-style-type: none;
    position: relative;
    margin-left: 58px;
}
.header__city {
    &::before {
        content: "";
        display: block;
        width: 15px;
        height: 21px;
        position: absolute;
        background-image: url("data:img/png;base64,");
        left: -28px;
    }
}
.header__tel {
    text-decoration: none;
    color: $white;
    margin-left: 36px;
    letter-spacing: 0.4px;
    transition: 0.3s ease;
    &:hover {
        color: $violet;
    }
    &::before {
        content: "";
        display: block;
        width: 21px;
        height: 21px;
        position: absolute;
        background-image: url("data:img/png;base64,");
        left: 0;
    }
}