.footer {
    border-top: 1px solid $black;
    padding-top: 53px;
    margin-bottom: 52px;
    .logo {
        width: 179px;
        height: 48px;
    }
}
.logo-black {
    flex: 0 0 10%;
    width: 140px;
    height: 23px;
    background-image: url(../../../img/svg/logo-black.svg);
    background-position: center;
    background-repeat: no-repeat;
}
.nav-footer {
    align-self: flex-end;
    flex: 0 0 58%;
}
.box-footer {
    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 21px;
    }
    &__link {
        color: $black;
    }
    &__tel {
        text-decoration: none;
        font-size: 2.125rem;
        font-weight: 500;
        color: $black;
        transition: 0.3s ease;
        &:hover {
            color: $violet;
        }
    }
}
.box-footer-form__input {
    display: flex;
    margin-top: 4px;
    margin-bottom: 13px;
}
.form {
    flex: 0 0 70%;
    &__text {
        opacity: 0.8;
    }
    &__link {
        text-decoration: none;
        color: $black;
        opacity: 0.8;
        &:hover {
            opacity: 1;
        }
    }
    &__input {
        display: block;
        width: 540px;
        box-sizing: border-box;
        border: 1px solid $black;
        padding: 18px 31px;
        transition: 0.2s ease;
        &:hover {
            border: 1px solid $violet;
        }
        &:focus {
            outline: none;
            
        }
    }
}
.box-footer-social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    margin-top: -10px;
    &__item {
        margin: 0px 14px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        width: 50px;
        height: 50px;
        border: none;
        border-radius: 100%;
        background-color: $black;
        background-repeat: no-repeat;
        display: block;
        background-position: center center;
        transition: 0.3s ease;
        &:hover {
            background-color: $violet;
        }
    }
    &__vk {
        background-image: url("data:img/png;base64,");
        background-size: 23px 14px;
    }
    &__inst {
        background-image: url("data:img/png;base64,");
        background-size: 18px 18px;
    }
    &__fb {
        background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAASCAYAAACJgPRIAAAAhElEQVQokeWQMQqDUBBEXyRg4RlSxTq1lVfyAF7Go1hZ6wVCCOmTMoIw8smXTBpN78DC7O5jWQZJXqWkTtJbH93CzoGLLeVQwlcVkFr/Ah7BHG14Nt8DBTCGxi+5hgVYg350kNTHQQ5k0T+Be/TXAGnjUL0FTcDJc2otn8ZD/uvxvUPADH2ic3hM15HMAAAAAElFTkSuQmCC");
        background-size: 9px 18px;
    }
}


.box-copyright {
    display: flex;
    justify-content: space-between;
    margin-top: 38px;
    &__text {
        position: relative;
        opacity: 0.5;
        font-size: 0.75rem;
    }
    &__link {
        text-decoration: none;
        color: $black;
        opacity: 0.5;
        transition: 0.2s ease;
        &:hover {
            opacity: 1;
        }
    }
}