@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";


:root {
    font-size: 16px;
}
a {
    text-decoration: none;
    &:hover {
        color: $black;
        text-decoration: none;
    }
}

body {
    font-family: 'Roboto', sans-serif;
    color: $black;
    overflow-x: hidden;
}

.wrapper {
    max-width: 1430px;
    margin: 0 auto;
    @media (max-width: 1330px) {
        max-width: 1140px;
    }
    @media (max-width: 1200px) {
        max-width: 960px;
    }
}
.title {
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 37px;
    padding: 0;
}
.text {
    text-align: center;
    line-height: 22px;
    padding: 0;
    &-apartment {
        margin-bottom: 59px;
        margin-top: -2px;
    }
    &-plan {
        margin-top: -7px;
    }
    &-company {
        line-height: 24px;
        margin-top: -8px;
    }
    &-news {
        margin-top: -12px;
    }
}
.section-main {
    background-image: url(../img/bg-1.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 39px;
}

.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 174px;
    &__title {
        font-size: 3.75rem;
        color: $whiteH1;
        padding-bottom: 31px;
        text-align: center;
    }
    &__text {
        font-weight: 500;
        line-height: 1.5;
        color: $white;
        max-width: 35rem;
        text-align: center;
        padding-bottom: 44px;
    }
}
.btn {
    color: $black;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 1.125rem;
    letter-spacing: 3px;
    background-color: $whiteH1;
    border: 1px solid $whiteH1;
    padding: 1.44rem 3.4rem;
    cursor: pointer;
    outline: none;
    border-radius: 0;
    transition: 0.3s ease;
    &:hover {
        color: $violet;
    }
    &-black {
        padding: 1rem 3.1rem;
        color: $whiteH1;
        background-color: $black;
        border: 1px solid $black;
        &:hover {
            color: $whiteH1;
            background-color: $violet;
            border: 1px solid $violet;
        }
    }
    &-loat {
        border: none;
        background-color: transparent;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        padding: 79px 23px 0 0;
        position: relative;
        opacity: 0.8;
        cursor: pointer;
        transition: 0.3s ease;
        &:hover {
            opacity: 1;
            &::after {
                background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAMCAYAAACulacQAAAAo0lEQVQYlV3QL4pCYRSG8Z9/kjZBMAxYrIJYRDFonBWozAosJk0uwAVYDDPBKILJpibBJm5gtjBGq8MH98LHfdrhOQfe9+RazfYTGyzxFpHHFHP8oBDLInb4wwFVjPCSXAbOGKKDEyqxDNzRQw1XfMQy8JsslHHLysAEdWxDoJQcVlhghnUqQ4VvfGGMfVqllAx9fOIS9wzRGxjgkf3QEd2sgH/pcBlvUpoltwAAAABJRU5ErkJggg==");
            }
        }
        &:focus {
            outline: none;
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 8px;
            height: 15px;
            right: 0;
            top: calc(50% - -33px);
            background-image: url("data:img/png;base64,");
            background-repeat: no-repeat;
        }
    }
    &-footer {
        padding: 16px 44px;
    }
}
.slider-main {
    .slick-dots {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 166px;
        cursor: pointer;
        
    }
    .slick-dots li {
        list-style-type: none;
        margin: 0 6px;
        
        &.slick-active button {
            background-color: $whiteH1;
        }
    }
    .slick-dots button {
        width: 10px;
        height: 10px;
        font-size: 0;
        background-color: rgba($whiteH1, 0.4);
        outline: none;
        transition: 0.3s ease;
        padding: 0;
    }
}
    
.section-filtr {
    margin: 10px 0 129px -29px;
}
.filtr {
    display: grid;
    justify-items: center;
    align-items: center;
    &-item {
        display: flex;
        margin: 30px 0 0 34px;
    }
    &-1 {
        position: relative;
    }
    &-2-3 {
        display: flex;
        justify-content: space-between;
    }
    &-4 {
        margin-top: 33px;
    }
    &-5 {
        align-items: center;
        margin-top: 60px;
        margin-left: 0;
    }
    &-all {
        height: 59px;
        border: 1px solid $black;
        text-align: center;
        padding: 17px;
    }
    &__text {
        &-1 {
        color: $black;
        text-decoration: none;
        position: relative;
        margin: 20px;
        padding: 0 28px 0 0;
        }
        &-3 {
            padding: 0 0 0 91px;
            position: relative;
            &::before {
                content: "";
                display: block;
                position: absolute;
                width: 85px;
                height: 8px;
                top: 6px;
                right: 77px;
                border-bottom: 1px solid rgba($black, 0.3);
            }
        }
        &-4 {
            position: relative;
            padding: 14px 48px 0 82px;
            transition: 0.3s ease;
            cursor: pointer;
            &:hover {
                color: $violet;
                &::after {
                    background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAaElEQVQYlYXPzQ2AMAgF4Cdxps7EAKYDePDkAD05UKcyJGAokPiO8IWf7ezXA2AAmKjTADApOrQQ07Q3SCfdBTYkvUlajHhBAnY3wWPJhyQ28Tce+nXpZoPxpvSgwHR49aBALtCKAX4BrkEfvgLjYGkAAAAASUVORK5CYII=");
                }
            }
            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 10px;
                height: 10px;
                top: 21px;
                left: 233px;
                background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAg0lEQVQYlWM00DWax8DAMIOBgeEUA3ZgxsDAkMEEVVQCFUAHZlC5GcwS4pJPGRgYHkIFQPRTqGKYoh6QbSCFDFBJZMXSyIpACliQrAIJgCRACkAArggEQG4kCiArRHYTzGS4B2FuRHE4Fjc/BSlEVwQDKIpBCpsZGBgm4QhwiGIGhgwAi/Im6H+tupwAAAAASUVORK5CYII=");
                background-repeat: no-repeat;
                transform: rotate(90deg);
                
            }
        }
    }
    &-district {
        width: 318px;
        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 8px;
            height: 15px;
            top: 23px;
            right: 129px;
            background-image: url("data:img/png;base64,");
            background-repeat: no-repeat;
            transform: rotate(90deg);
            cursor: pointer;
        }
    }
    &-type {
        width: 319px;
        border-right-width: 0px;
        /*&__menu {
            position: absolute;
            width: 300px;
            background-color: $whiteH1;
            top: 40px;
            left: 10px;
        }
        &__list {
            list-style: none;
        }
        &__link {
            color: $black;
            &:hover {
                color: $violet;
            }
        }*/
        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 8px;
            height: 15px;
            top: 23px;
            left: 204px;
            background-image: url("data:img/png;base64,");
            background-repeat: no-repeat;
            transform: rotate(90deg);
            cursor: pointer;
        }
    }
    &-area {
        width: 236px;
        border-right-width: 0px;
        &__size {
            width: 360px;
            border-bottom: 1px solid $black;
            margin-right: 150px;
        }
    }
    &-price {
        width: 236px;
        border-right-width: 0px;
        &__value {
            width: 360px;
            border-bottom: 1px solid $black;
        }
    }
    &__unit {
        width: 245px;
    }
}
.section-house {
    margin-bottom: 153px;
}
.slider {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media (max-width: 1270px) {
        justify-content: center;
    }
}
.slider-house {
    margin-top: 127px;
    &__item {
        width: 461px;
        border: 1px solid $black;
    }
    &-img {
        height: 415px;
        color: $whiteH1;
        &:hover {
            background-size: 110%;
        }
        &__text {
            padding: 305px 0 0 30px;
            position: relative;
            transition: 0.3s ease;
            &:hover {
               &::after {
                   background-color: $violet;
               } 
            }
            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 40px;
                height: 40px;
                right: 26px;
                bottom: -2px;
                background-color: $black;
                background-image: url("data:img/png;base64,");
                background-repeat: no-repeat;
                background-position: center;
                cursor: pointer;
            }
        }
        &__lingonberry {
            background: url(../img/lingonberry.png) center center/cover no-repeat;
        }
        &__trend {
            background: url(../img/trend.png) center center/cover no-repeat;
        }
        &__panorama {
            background: url(../img/panorama.png) center center/cover no-repeat;
        }
        &__title {
            font-size: 1.9rem;
            font-weight: 700;
        }
        &__address {
            margin-top: 8px;
        }
    }
    &-info {
        height: 127px;
        padding: 3px 99px 5px 28px;
        &__data {
            display: flex;
            justify-content: space-between;
            margin-top: 27px;
        }
        &__home {
            position: relative;
            padding-left: 35px;
            &::before {
                content: "";
                display: block;
                position: absolute;
                width: 18px;
                height: 18px;
                top: -3px;
                left: 3px;
                background-image: url("data:img/png;base64,");
                background-repeat: no-repeat;
            }
        }
        &__area {
            position: relative;
            &::before {
                content: "";
                display: block;
                position: absolute;
                width: 18px;
                height: 18px;
                top: -3px;
                left: -32px;
                background-image: url("data:img/png;base64,");
                background-repeat: no-repeat;
            }
        }
        &__painting {
            position: relative;
            &::before {
                content: "";
                display: block;
                position: absolute;
                width: 18px;
                height: 18px;
                top: -3px;
                left: -33px;
                background-image: url("data:img/png;base64,");
                background-repeat: no-repeat;
            }
        }
        &__price {
            font-size: 1.9rem;
            font-weight: 700;
            margin-top: 11px;
        }
    }
}

.section-apartment {
    margin-bottom: 151px;
}
.slider-apartment {
    margin-top: 24px;
    &-1 {
        background: url(../img/apartment-1.png) center center/cover no-repeat;
    }
    &-2 {
        background: url(../img/apartment-2.png) center center/cover no-repeat;
    }
    &-3 {
        background: url(../img/apartment-3.png) center center/cover no-repeat;
    }
    &-4 {
        background: url(../img/apartment-4.png) center center/cover no-repeat;
    }
    &-5 {
        background: url(../img/apartment-5.png) center center/cover no-repeat;
    }
    &-6 {
        background: url(../img/apartment-6.png) center center/cover no-repeat;
    }
    &-info__painting {
        padding-right: 123px;
    }
}
.section-plan {
    margin-bottom: 151px;
}
.plan {
    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 83px 0 59px 41px;
    }
    &-link {
        font-size: 1.125rem;
        color: rgba($black, 0.3);
        text-decoration: none;
        text-transform: uppercase;
        font-weight: 500;
        letter-spacing: 3px;
        transition: 0.3s ease;
        position: relative;
        padding: 0 60px;
        &:first-child {
            &::after {
                content: "";
                display: block;
                position: absolute;
                background-color: $black;
                width: 1px;
                height: 24px;
                top: 2px;
                left: 344px;
            }
        }
        &:hover {
            color: $black;
        }
    }
    &-img {
        width: 100%;
        height: 630px;
        background: url(../img/plan.png) center center/cover no-repeat;
    }
}
.section-company {
    margin-bottom: 188px;
}
.company-item {
    margin-left: 5px;
    margin-right: 20px;
}
.company-list {
    width: 1110px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    margin: 0 auto;
    &__item {
        font-size: 0.75rem;
        text-transform: uppercase;
        font-weight: 500;
        line-height: 1.5;
        letter-spacing: 2px;
        text-align: center;
        max-width: 160px;
        padding: 0 5px;
    }
    span {
        display: block;
        width: 58px;
        height: 58px;
        margin: 44px;
    }
    &__sketch {
        background-image: url("data:img/png;base64,");
        background-repeat: no-repeat;
        background-position: center center;
    }
    &__price {
        background-image: url("data:img/png;base64,");
        background-repeat: no-repeat;
        background-position: center center;
        padding-bottom: 12px;
    }
    &__time {
        background-image: url("data:img/png;base64,");
        background-repeat: no-repeat;
        background-position: center center;
    }
    &__quality {
        background-image: url("data:img/png;base64,");
        background-repeat: no-repeat;
        background-position: center center;
    }
}
.section-news {
    margin-bottom: 201px;
}
.slider-news {
    display: flex;
    justify-content: space-between;
    margin-top: 122px;
    &__item {
        width: 461px;
        height: 398px;
        border: 1px solid $black;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        &:hover {
            background: url(../img/news.png) center center/cover no-repeat;
            color: $whiteH1;
            &::after {
                background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAMCAYAAACulacQAAAAk0lEQVQYlW3QMWpCcRDE4cmrUllYCEFSBy/gCUS8gXVqyRWSI4RcIGBrJ1gpBJJKcqlPVl7x/OPCbPOD2ZkNfrDBAzJUrSXOeEfXwtIcf/jEYwtLM5zwjVELS8/YY4fJNUQz4yTbJF3XkiSrJE9JDkPLcnnDP9bDm1Xho6+0GKat6F/47Svd9KzoR7zc+9Arpi1ALi1B5AX1Ig8oAAAAAElFTkSuQmCC");
            }
        }
    }
}
.news {
    &-text {
        padding: 0px 22px 19px 30px;
        &__data {
            font-size: 0.75rem;
            font-weight: 500;
            opacity: 0.6;
            margin-bottom: 10px;
        }
        &__description {
            line-height: 1.5;
            width: 90%;
        }
        &__btn {
            opacity: 0.8;
            position: relative;
            cursor: pointer;
            padding-top: 8px;
            &:hover {
                &::after {
                    background-image: url("data:img/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAMCAYAAACulacQAAAAk0lEQVQYlW3QMWpCcRDE4cmrUllYCEFSBy/gCUS8gXVqyRWSI4RcIGBrJ1gpBJJKcqlPVl7x/OPCbPOD2ZkNfrDBAzJUrSXOeEfXwtIcf/jEYwtLM5zwjVELS8/YY4fJNUQz4yTbJF3XkiSrJE9JDkPLcnnDP9bDm1Xho6+0GKat6F/47Svd9KzoR7zc+9Arpi1ALi1B5AX1Ig8oAAAAAElFTkSuQmCC");
                }
            }
            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 8px;
                height: 15px;
                top: 12px;
                left: 150px;
                background-image: url("data:img/png;base64,");
                background-repeat: no-repeat;
            }
        }
    }
    
}